import React from 'react';
import Autosuggest from 'react-autosuggest';
import suggestData from './SuggestData'
import theme from './Suggest.module.css';
import arrow from './arrow.svg'
import remove from './remove.svg'
import friend from './friend.svg'
import group from './group.svg'
import game from './game.svg'

// When suggestion is clicked, Autosuggest needs to populate the input
// based on the clicked suggestion. Teach Autosuggest how to calculate the
// input value for every given suggestion.
const getSuggestionValue = suggestion => suggestion.text;

// Use your imagination to render suggestions.
const renderSuggestion = (suggestion, query) => {
    let s = suggestion.text.text;
    let p = s.slice(0, query.query.length);
    let e = s.slice(query.query.length);

    return (
            <div>
            <b>{p}</b>{e}
            <div className="arrow">
                <img src={arrow} alt="arrow"/>
            </div>
        </div>
    );
};

const shouldRenderSuggestions = () => {
    return true;
};

class Suggest extends React.Component {
    constructor() {
        super();

        // Autosuggest is a controlled component.
        // This means that you need to provide an input value
        // and an onChange handler that updates this value (see below).
        // Suggestions also need to be provided to the Autosuggest,
        // and they are initially empty because the Autosuggest is closed.
        this.state = {
            value: '',
            suggestions: []
        };
    }

    onChange = (event, {newValue}) => {
        this.setState({
            value: newValue
        });
    };

    // Autosuggest will call this function every time you need to update suggestions.
    // You already implemented this logic above, so just use it.
    onSuggestionsFetchRequested = ({value}) => {
        fetch(this.getUrl(value))
            .then(response => response.json())
            .then(data => {
                console.log(data);
                this.setState({ suggestions: data })
            })
    };

    // Autosuggest will call this function every time you need to clear suggestions.
    onSuggestionsClearRequested = () => {
        this.setState({ suggestions: [] });
    };

    getUrl = (value) => {
        var h = window.location.hash;

        if (h !== undefined && h.length > 1) {
            h = h.substr(1);
            var url = h.substr(0, h.indexOf(","));
            var uid = h.substr(h.indexOf(",") + 1);

            console.debug(url);
            console.debug(uid);

            return url + "?userId=" + uid + "&input=" + value;
        } else {
            return `http://vcdevm80.mail.msk:8080/userSuggest?userId=3845528324706&input=${value}`
        }
    };

    render() {
        const {value, suggestions} = this.state;

        // Autosuggest will pass through all these props to the input.
        const inputProps = {
            placeholder: 'Поиск',
            value,
            onChange: this.onChange
        };

        // Finally, render it!
        return (
            <Autosuggest
                suggestions={suggestions}
                onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                getSuggestionValue={getSuggestionValue}
                shouldRenderSuggestions={shouldRenderSuggestions}
                renderSuggestion={renderSuggestion}
                inputProps={inputProps}
                theme={theme}
                alwaysRenderSuggestions={true}
            />
        );
    }
}

export default Suggest;